<h1 id="nodejs安装说明" tabindex="-1"><a class="header-anchor" href="#nodejs安装说明" aria-hidden="true">#</a> Nodejs安装说明</h1>
<p><code>Node.js</code>是一个跨平台的JavaScript运行时环境，允许服务端运行在JavaScript代码上。</p>
<p><code>npm</code> <strong>Node Package Manager</strong>，是默认的node.js的包管理工具，并且是世界上最大的开源Node.js发布软件存储库。</p>
<h2 id="使用yum安装" tabindex="-1"><a class="header-anchor" href="#使用yum安装" aria-hidden="true">#</a> 使用Yum安装</h2>
<p>使用<strong>yum</strong>包管理工具安装Node.js和npm。如果使用Node.js仅仅是开发Node.js应用，那么最简单的安装方式就是通过<code>yum</code>安装。</p>
<div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<p>添加node的依赖仓库，将NodeSource仓库添加到系统</p>
</div>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">curl</span> -sL https://rpm.nodesource.com/setup_10.x <span class="token operator">|</span> <span class="token function">sudo</span> <span class="token function">bash</span> -
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> yum <span class="token function">install</span> nodejs
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>校验安装是否成功</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>node --version
<span class="token function">npm</span> --version
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="使用nvm安装" tabindex="-1"><a class="header-anchor" href="#使用nvm安装" aria-hidden="true">#</a> 使用NVM安装</h2>
<p>NVM（<strong>Node Version Manager</strong>）是一个bash脚本用来管理多版本Node.js。NVM允许我们安装或卸载指定的Node.js版本，这也就意味着我们可以使用我们自己想要的Node.js版本。</p>
<blockquote>
<p>We strongly recommend using a Node version manager to install Node.js and npm.</p>
</blockquote>
<p>安装NVM</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">curl</span> -o- https://raw.githubusercontent.com/creationix/nvm/v0.34.0/install.sh <span class="token operator">|</span> <span class="token function">bash</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>OR</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">wget</span> -qO- https://raw.githubusercontent.com/creationix/nvm/v0.34.0/install.sh <span class="token operator">|</span> <span class="token function">bash</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>使用nvm安装Node.js</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nvm <span class="token function">install</span> node
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>查看所有版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nvm list-remote
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>安装多个版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nvm <span class="token function">install</span> v8.14.1
nvm <span class="token function">install</span> v10.14.2
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>查看已安装的版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nvm <span class="token function">ls</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>改变当前使用的版本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nvm use v8.14.1
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>使用yum安装更简单，使用nvm可以使用多版本的node.js。</p>
<h1 id="ubuntu上安装nodejs" tabindex="-1"><a class="header-anchor" href="#ubuntu上安装nodejs" aria-hidden="true">#</a> ubuntu上安装nodejs</h1>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">apt</span> update
<span class="token function">sudo</span> <span class="token function">apt</span> <span class="token function">install</span> nodejs
<span class="token function">sudo</span> <span class="token function">apt</span> <span class="token function">install</span> <span class="token function">npm</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>nodejs --version
<span class="token function">npm</span> --version
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="更新-npm-and-node-js" tabindex="-1"><a class="header-anchor" href="#更新-npm-and-node-js" aria-hidden="true">#</a> 更新 npm and node.js</h2>
<ol>
<li>see what version of npm you're running</li>
</ol>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>npm <span class="token operator">-</span>v
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="2">
<li>upgrading on Linux</li>
</ol>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>sudo npm install <span class="token operator">-</span>g npm
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="mac上安装nodejs" tabindex="-1"><a class="header-anchor" href="#mac上安装nodejs" aria-hidden="true">#</a> mac上安装nodejs</h2>
<p>使用nvm安装</p>
